@font-face {
  font-family: "NeueHaasDisplay";
  src: url("../fonts/NeueHaasDisplay-Medium.woff2") format("woff2"), url("../fonts/NeueHaasDisplay-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "NeueHaasDisplay";
  src: url("../fonts/NeueHaasDisplay-Bold.woff2") format("woff2"), url("../fonts/NeueHaasDisplay-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  height: 100%;
  box-sizing: border-box; }

body {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: "NeueHaasDisplay", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  color: #000000;
  background: #f5f5f5; }

main {
  flex-grow: 1; }

header {
  flex-grow: 0; }

footer {
  flex-grow: 0; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.hidden {
  display: none !important; }

.container {
  margin: 0 auto;
  width: 1000px;
  max-width: 100%; }

.header {
  padding-top: 17px;
  padding-bottom: 17px;
  background-color: #fff;
  border-top: 1px solid #000000;
  border-bottom: 1px solid #000000; }

.header__row {
  display: flex;
  align-items: center;
  justify-content: space-between; }

.header__logo {
  display: flex;
  align-items: center; }
  .header__logo img {
    display: block;
    width: 91px;
    height: auto;
    margin-right: 12px; }
  .header__logo span {
    display: block;
    width: 150px;
    max-width: 100%;
    font-size: 26px;
    font-weight: 500;
    text-transform: uppercase; }
  .header__logo:hover, .header__logo:focus, .header__logo:active {
    cursor: pointer;
    opacity: 0.6; }

.header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  align-items: center; }

.header__item {
  margin-left: 4px; }

.header__link {
  display: flex;
  align-items: center;
  justify-content: center; }
  .header__link:hover, .header__link:focus, .header__link:active {
    cursor: pointer;
    opacity: 0.6; }
  .header__link img {
    width: 65px;
    height: auto; }

@media screen and (max-width: 1100px) {
  .header__row {
    padding-left: 15px;
    padding-right: 15px;
    flex-wrap: wrap; } }

@media screen and (max-width: 760px) {
  .header__row {
    display: block; }
  .header__logo {
    margin-bottom: 25px; }
  .header__item {
    margin-left: 0;
    margin-right: 4px; } }

.main {
  padding-top: 22px;
  padding-bottom: 110px; }

.main__caption {
  margin: 0;
  margin-bottom: 12px;
  font-weight: 400;
  letter-spacing: -.012em; }

.main__container {
  padding-left: 50px;
  padding-right: 50px; }

.main__video {
  width: 100%;
  outline: none; }

@media screen and (max-width: 760px) {
  .main {
    padding-bottom: 25px; }
  .main__caption {
    margin-top: 35px;
    margin-bottom: 55px; } }

@media screen and (max-width: 576px) {
  .main__container {
    padding-left: 15px;
    padding-right: 15px; }
  .main__video {
    height: 350px; } }

.footer {
  padding: 30px; }

.footer__copy {
  margin: 0;
  text-align: center; }

.popup {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 3;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.6); }

.popup__form {
  display: flex;
  flex-direction: column;
  padding: 30px;
  background-color: #fff;
  border-radius: 5px; }

.popup__text {
  margin: 0;
  margin-bottom: 25px;
  text-align: center; }

.popup__input {
  display: flex;
  align-items: center;
  height: 45px;
  padding: 5px;
  padding-left: 19px;
  margin-bottom: 15px; }

.popup__btn {
  outline: none;
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70%;
  height: 50px;
  margin: 0 auto;
  font-size: 25px;
  color: #fff;
  border-radius: 5px;
  background-color: #505050; }
  .popup__btn:hover, .popup__btn:focus, .popup__btn:active {
    cursor: pointer;
    opacity: 0.6; }
